import * as Yup from 'yup';

export const operationFormSchema = Yup.object({
  title: Yup.string().required(' ').min(2, '  '),

  amount: Yup.number()
    .typeError(' ')
    .positive('   ')
    .required(' '),

  category: Yup.string().required(' '),

  date: Yup.string()
    .required(' ')
    .matches(/\d{4}-\d{2}-\d{2}/, '  '),
});
